import React, { Component, Suspense } from 'react';
import moment from 'moment'
import { Button } from 'react-bootstrap';
import DateRangePicker from 'react-bootstrap-daterangepicker';
import 'bootstrap-daterangepicker/daterangepicker.css';
import CollectionThumb from '../../../CommonComponents/CollectionThumb';

class SubHeader extends Component {
    state = {
        startDate: moment().subtract(29, 'days').format("DD-MM-YYYY"),
        endDate: moment().format("DD-MM-YYYY"),
        maxDate: moment().endOf('month').format("DD-MM-YYYY"),
    }
    handleOnApply = (event, picker) => {
        this.props.dateRangesFilter(picker.startDate.format("YYYY-MM-DD"), picker.endDate.format("YYYY-MM-DD"))
        this.setState({ startDate: picker.startDate.format("DD-MM-YYYY"), endDate: picker.endDate.format("DD-MM-YYYY") })
    }
    handleCallback = (start, end) => {
        this.props.dateRangesFilter(start.format("YYYY-MM-DD"), end.format("YYYY-MM-DD"))
        this.setState({ startDate: start.format("DD-MM-YYYY"), endDate: end.format("DD-MM-YYYY") })
    };
    render() {
        const { startDate, endDate, maxDate } = this.state
        const { searchInput, searchOpen, textFilter, onClickToSearch, onClickToClear, name, thumbnails } = this.props
        return <> <div className="position-fixed bg-white border-bottom subheader">
            <div className="container-fluid d-flex align-items-center justify-content-between flex-wrap h-100">
                <div className="d-flex flex-wrap">
                    {name ? <CollectionThumb symbolClasses={'symbol symbol-24 bg-light me-2'} iconClass={'text-gray-700 me-0 fw-normal'} thumb={thumbnails} thumbName={name} /> : null}
                    <h1 className="text-dark text-start font-weight-bold me-2 fs-6 mb-0">{name ? name : `UGC Library`}</h1>
                </div>
                <div className="d-flex align-items-center flex-wrap">
                    <ul className="list-inline d-flex align-items-center flex-nowrap mb-0 m_action_icons">
                        <li className="assets_search">
                            <Button variant='link' className="text-decoration-none btn-icon btn-sm btn-active-light btn-active-color-primary transition-none border-0 flex-shrink-0 me-2" onClick={searchInput}>
                                <i className="ri-search-line icon-6 me-0"></i>
                            </Button>
                            <div className="assets_search_input position-absolute" style={{ display: searchOpen ? 'block' : 'none' }}>
                                <div id="inputSpinner" className="p-0 spinner-gray spinner-sm">
                                    <input id="assets_search_input" type="text" className="form-control form-control-sm pe-9 w-100" placeholder="What are you looking for?" />
                                </div>
                                <Button id="inputCloseButton" variant='link' className="text-decoration-none btn-sm position-absolute end-0 top-50 translate-middle-y px-2" onClick={onClickToClear}>
                                    <i className="ri-close-line icon-6 me-0 text-gray-600"></i>
                                </Button>
                            </div>
                        </li>
                        <li>
                            <DateRangePicker className="ugc-datepicker" onApply={this.handleOnApply} startDate={startDate} endDate={endDate} initialSettings={{
                                locale: { format: 'DD-MM-YYYY', separator: '-' },
                                maxDate: maxDate,
                                showDropdowns: true,
                                startDate: startDate, endDate: endDate,
                                ranges: {
                                    Today: [moment().format("DD-MM-YYYY"), moment().format("DD-MM-YYYY")],
                                    Yesterday: [
                                        moment().subtract(1, 'days').format("DD-MM-YYYY"),
                                        moment().subtract(1, 'days').format("DD-MM-YYYY"),
                                    ],
                                    'Last 7 Days': [
                                        moment().subtract(6, 'days').format("DD-MM-YYYY"),
                                        moment().format("DD-MM-YYYY"),
                                    ],
                                    'Last 30 Days': [
                                        moment().subtract(29, 'days').format("DD-MM-YYYY"),
                                        moment().format("DD-MM-YYYY"),
                                    ],
                                    'This Month': [
                                        moment().startOf('month').format("DD-MM-YYYY"),
                                        moment().endOf('month').format("DD-MM-YYYY"),
                                    ],
                                    'Last Month': [
                                        moment().subtract(1, 'month').startOf('month').format("DD-MM-YYYY"),
                                        moment().subtract(1, 'month').endOf('month').format("DD-MM-YYYY"),
                                    ],
                                },
                            }}
                            >
                                <Button variant='link' className="text-decoration-none btn-icon btn-sm btn-active-light btn-active-color-primary transition-none border-0 me-2">
                                    <i className="ri-calendar-2-line icon-6 me-0"></i>
                                </Button>
                            </DateRangePicker>

                        </li>
                        <li>
                            <button id="filter_aside" type="button" className="btn btn-icon btn-sm btn-active-light btn-active-color-primary transition-none border-0">
                                <i className="ri-filter-line icon-6 me-0"></i>
                            </button>
                        </li>
                    </ul>
                </div>
            </div>
        </div></>
    }

}
export default SubHeader